@font-face {
  font-family: "proza_libre";
  src: url("/assets/fonts/prozalibre-bold-webfont.woff2") format("woff2"), url("/assets/fonts/prozalibre-bold-webfont.woff") format("woff");
  font-weight: bold;
  font-style: normal;
}
@font-face {
  font-family: "proza_libre";
  src: url("/assets/fonts/prozalibre-regular-webfont.woff2") format("woff2"), url("/assets/fonts/prozalibre-regular-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "proza_libre";
  src: url("/assets/fonts/prozalibre-italic-webfont.woff2") format("woff2"), url("/assets/fonts/prozalibre-italic-webfont.woff") format("woff");
  font-weight: normal;
  font-style: italic;
}
:root {
  --color-1: #fefefe;
  --color-3: #486bc2;
}

html {
  font-family: "proza_libre", var(--font-stack-body);
}

h1 {
  max-width: 15rem;
  margin-right: auto;
  margin-left: auto;
}

main {
  margin-top: 0;
}

@media (min-width: 800px) {
  figure {
    width: 40em;
    margin-left: -5em;
  }
}